.app-container {
  display: flex;
  flex-direction: row;
}

.navigation {
  width: 40%;
  padding: 20px;
  border-right: 1px solid #ccc;
}

.main-content {
  flex: 1;
  padding: 20px;
}

.article-list {
  margin-bottom: 20px;
}

.article-list ul {
  list-style-type: none;
  padding: 0;
}

.article-list li {
  cursor: pointer;
  margin-bottom: 10px;
}

.tabs {
  display: none; /* Hide tabs by default for desktop view */
}

@media screen and (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }

  .navigation {
    width: 100%;
    border-right: none;
  }

  .tabs {
    display: block; /* Show tabs for mobile view */
    margin-top: 20px;
  }
}
